import authenticatedApi from "./authenticatedApi";

class DesignService {
  getSurveyDesign() {
    const surveyId = sessionStorage.getItem("surveyId");
    return authenticatedApi
      .get(`/survey/${surveyId}/design`)
      .then((response) => {
        return response.data;
      });
  }

  setSurveyDesign(data, params) {
    const surveyId = sessionStorage.getItem("surveyId");
    return authenticatedApi
      .post(`/survey/${surveyId}/design`, data, { params })
      .then((response) => {
        return response.data;
      });
  }

  publish(params) {
    const surveyId = sessionStorage.getItem("surveyId");
    return authenticatedApi
      .post(`/survey/${surveyId}/design/publish`, {}, { params })
      .then((response) => {
        return response.data;
      });
  }

  uploadResource(file) {
    const surveyId = sessionStorage.getItem("surveyId");
    const formData = new FormData();
    formData.append("file", file);
    return authenticatedApi
      .post(`/survey/${surveyId}/resource`, formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "multipart/form-data",
        },
      })
      .then((response) => {
        return response.data;
      });
  }
}

export default new DesignService();
