import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@mui/material";
import React from "react";
import { useTranslation } from "react-i18next";

function ErrorDialog({ setErrorSeen, error }) {
  const { t } = useTranslation("manage");

  return (
    <Dialog
      open={true}
      onClose={() => setErrorSeen()}
      aria-labelledby="alert-dialog-title-error"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title-error">Error</DialogTitle>
      <DialogContent>{t("processed_errors." + error.name)}</DialogContent>
      <DialogActions>
        <Button onClick={() => setErrorSeen()}>OK</Button>
      </DialogActions>
    </Dialog>
  );
}

export default ErrorDialog;
